@font-face {
  font-family: "poppins";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/poppins-regular-webfont.woff2") format("woff2"), url("../fonts/poppins-regular-webfont.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "poppins";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/poppins-medium-webfont.woff2") format("woff2"), url("../fonts/poppins-medium-webfont.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "poppins";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/poppins-bold-webfont.woff2") format("woff2"), url("../fonts/poppins-bold-webfont.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "cormorantgaramond";
  font-style: italic;
  font-weight: 400;
  src: url("../fonts/cormorantgaramond-italic-webfont.woff2") format("woff2"), url("../fonts/cormorantgaramond-italic-webfont.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "cormorantgaramond";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/cormorantgaramond-semibold-webfont.woff2") format("woff2"), url("../fonts/cormorantgaramond-semibold-webfont.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "cormorantgaramond";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/cormorantgaramond-bold-webfont.woff2") format("woff2"), url("../fonts/cormorantgaramond-bold-webfont.woff") format("woff");
  font-display: swap;
}
html {
  box-sizing: border-box;
  font-size: 10px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "poppins", "Arial", sans-serif;
  font-size: 1.6rem;
  line-height: 130%;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  min-width: 320px;
  background-color: #ffffff;
  color: #1d1613;
}

.visually-hidden {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  overflow: hidden;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.button {
  border: none;
  outline: none;
}

.link {
  display: block;
  position: relative;
  text-decoration: none;
  color: inherit;
}

.link,
button {
  cursor: pointer;
}

.container {
  margin-right: auto;
  margin-left: auto;
  max-width: 1260px;
}
@media (min-width: 1024px) {
  .container {
    width: 92.24%;
    padding-left: 45px;
    padding-right: 45px;
  }
}
@media (max-width: 1023px) {
  .container {
    width: 92.1875%;
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media (max-width: 767px) {
  .container {
    width: 84.375%;
    padding-left: 0;
    padding-right: 0;
  }
}

.page-navigation ul {
  display: flex;
}
.page-navigation li:not(:last-child) {
  margin-right: 2%;
  padding-right: 10px;
}
.page-navigation .link {
  opacity: 0.7;
}
.page-navigation .link:hover, .page-navigation .link:focus {
  text-decoration: underline;
}
.page-navigation .link:active {
  opacity: 1;
}

.header {
  outline: 1px dashed red;
  outline-offset: -1px;
  font-family: "poppins", "Arial", sans-serif;
  font-size: 1.4rem;
  line-height: 150%;
  font-weight: 500;
  color: inherit;
}
@media (min-width: 1024px) {
  .header {
    background-color: inherit;
  }
}
.header .container {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  padding-top: 30px;
}
@media (min-width: 1024px) {
  .header .container {
    margin-bottom: 19px;
  }
}
@media (max-width: 767px) {
  .header .container {
    padding-top: 20px;
  }
}
@media (min-width: 1024px) {
  .header .container > *:not(.header__logo):not(.header__form-box) {
    transform: translate(0, 13px);
  }
}
.header a {
  border-bottom: 1px dashed transparent;
}
.header a:hover, .header a:focus {
  border-color: inherit;
}
.header a:active {
  opacity: 0.5;
}

.header__form-box {
  position: relative;
}
@media (min-width: 1024px) {
  .header__form-box {
    width: 12.6984%;
    width: 140px;
    margin-left: 29px;
    margin-right: 32.8%;
    transform: translate(0, 10px);
  }
}
.header__form-box svg {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(-28px, -50%);
  fill: var(--icon-color);
}
@media (min-width: 1024px) {
  .header__form-box svg {
    --icon-color: #000000;
  }
}
@media (max-width: 1023px) {
  .header__form-box svg {
    --icon-color: #ffffff;
  }
}
.header__form-box input {
  font-family: "poppins", "Arial", sans-serif;
  font-size: 1.4rem;
  line-height: 150%;
  font-weight: 500;
  border-color: transparent;
  outline: none;
  background-color: transparent;
  width: 100%;
  padding-left: 0;
  padding-right: 0;
  border-width: 1px;
  border-style: dashed;
}
.header__form-box input:hover, .header__form-box input:focus {
  border-bottom-color: #000000;
}
.header__form-box input::placeholder {
  font-size: inherit;
  line-height: inherit;
}

.header__logo {
  fill: --icon-color;
}
@media (min-width: 1024px) {
  .header__logo {
    --icon-color: #a87b62;
    margin-right: auto;
  }
}
@media (max-width: 1023px) {
  .header__logo {
    --icon-color: #ffffff;
  }
}

@media (min-width: 1024px) {
  .header__login {
    margin-right: 3.1%;
  }
}

@media (min-width: 1024px) {
  .header__cart {
    margin-right: 2px;
  }
}

.header__part {
  width: 100%;
}
@media (min-width: 1024px) {
  .header__part {
    border-top: 1px solid #f6f5f4;
  }
}

.header__navigation {
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 1024px) {
  .header__navigation {
    width: 493px;
    padding-top: 18px;
    padding-bottom: 8px;
    transform: translate(5px, 0);
  }
}
.header__navigation ul {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}
@media (min-width: 1024px) {
  .header__navigation ul {
    width: 100%;
  }
}
@media (min-width: 1024px) {
  .header__navigation li {
    margin-bottom: 10px;
  }
  .header__navigation li:not(:last-child) {
    margin-right: 7.0994%;
  }
}
.header__navigation a {
  font-size: 1.6rem;
  line-height: 150%;
}

.hidden-entity {
  display: none;
}

@media (max-width: 1023px) {
  .hidden-before-desktop {
    display: none;
  }
}

@media (min-width: 1024px) {
  .hidden-on-desktop {
    display: none;
  }
}

@media (max-width: 767px) {
  .hidden-before-tablet {
    display: none;
  }
}

@media (min-width: 768px) {
  .hidden-after-mobile {
    display: none;
  }
}

@media (max-width: 767px) {
  .hidden-on-mobile {
    display: none;
  }
}